"use client";
import React, { useState, useEffect } from "react";
import { 
  Search, Plus, Users, MoreVertical, 
  Trash2, Edit3, Hash, ShieldCheck, 
  X, AlertCircle, Loader2
} from "lucide-react";
// import { Fetch } from '@/libs/api'; // Uncomment when connecting to your API

export default function Classes() {
  const [classes, setClasses] = useState([
    { id: 1, name: "JSS 2", code: "A", teacher: "Mr. Abraham Smith", students: 32 },
    { id: 2, name: "JSS 2", code: "B", teacher: "Mrs. Deborah Cole", students: 28 },
  ]);
  
  const [searchTerm, setSearchTerm] = useState("");
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [editingClass, setEditingClass] = useState<any>(null);
  const [deleteConfirm, setDeleteConfirm] = useState<number | null>(null);

  // Form State
  const [formData, setFormData] = useState({ name: "", code: "", teacher: "" });

  // --- ACTIONS ---

  const openCreateModal = () => {
    setEditingClass(null);
    setFormData({ name: "", code: "", teacher: "" });
    setIsModalOpen(true);
  };

  const openEditModal = (cls: any) => {
    setEditingClass(cls);
    setFormData({ name: cls.name, code: cls.code, teacher: cls.teacher });
    setIsModalOpen(true);
  };

  const handleSave = async (e: React.FormEvent) => {
    e.preventDefault();
    if (editingClass) {
      // Logic for Update (PUT)
      setClasses(classes.map(c => c.id === editingClass.id ? { ...c, ...formData, code: formData.code.toUpperCase() } : c));
    } else {
      // Logic for Create (POST)
      const newEntry = { id: Date.now(), ...formData, code: formData.code.toUpperCase(), students: 0 };
      setClasses([newEntry, ...classes]);
    }
    setIsModalOpen(false);
  };

  const handleDelete = (id: number) => {
    // Logic for Delete (DELETE)
    setClasses(classes.filter(c => c.id !== id));
    setDeleteConfirm(null);
  };

  const filteredClasses = classes.filter(c => 
    c.name.toLowerCase().includes(searchTerm.toLowerCase()) || 
    c.teacher.toLowerCase().includes(searchTerm.toLowerCase())
  );

  return (
    <div className="p-6 md:p-10 bg-[#F9FAFB] min-h-screen font-sans text-zinc-900">
      
      {/* HEADER */}
      <div className="flex flex-col md:flex-row md:items-center justify-between gap-4 mb-10">
        <div>
          <h1 className="text-2xl font-bold tracking-tight text-zinc-900">Class Management</h1>
          <p className="text-sm text-zinc-500">Create, edit, and delete school class arms.</p>
        </div>
        <button 
          onClick={openCreateModal}
          className="flex items-center justify-center gap-2 bg-zinc-900 text-white px-5 py-2.5 rounded-xl text-sm font-bold hover:bg-zinc-800 transition-all shadow-sm"
        >
          <Plus size={18} /> Create Class
        </button>
      </div>

      {/* STATS */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-10">
        <StatCard icon={<Hash size={20}/>} label="Total Classes" value={classes.length.toString()} color="text-blue-600" />
        <StatCard icon={<Users size={20}/>} label="Total Students" value="60" color="text-emerald-600" />
        <StatCard icon={<ShieldCheck size={20}/>} label="Teachers" value={classes.length.toString()} color="text-indigo-600" />
      </div>

      {/* TABLE SECTION */}
      <div className="bg-white border border-zinc-200 rounded-[2rem] overflow-hidden shadow-sm">
        <div className="p-6 border-b border-zinc-100">
          <div className="relative max-w-md">
            <Search className="absolute left-3 top-1/2 -translate-y-1/2 text-zinc-400" size={18} />
            <input 
              type="text" 
              placeholder="Search class or teacher..." 
              className="w-full pl-10 pr-4 py-2.5 bg-zinc-50 border border-zinc-200 rounded-xl text-sm outline-none focus:ring-4 focus:ring-zinc-100 transition-all"
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
            />
          </div>
        </div>

        <div className="overflow-x-auto">
          <table className="w-full text-left">
            <thead className="bg-zinc-50/50 text-[10px] uppercase tracking-[0.15em] text-zinc-400 font-bold">
              <tr>
                <th className="px-8 py-4">Designation</th>
                <th className="px-8 py-4">Class Teacher</th>
                <th className="px-8 py-4">Students</th>
                <th className="px-8 py-4 text-right">Actions</th>
              </tr>
            </thead>
            <tbody className="divide-y divide-zinc-100">
              {filteredClasses.length > 0 ? filteredClasses.map((item) => (
                <tr key={item.id} className="hover:bg-zinc-50/30 transition-colors">
                  <td className="px-8 py-5">
                    <div className="flex items-center gap-4">
                      <div className="w-10 h-10 rounded-xl bg-zinc-900 text-white flex items-center justify-center font-bold text-xs">
                        {item.code}
                      </div>
                      <span className="text-sm font-bold text-zinc-800">{item.name} {item.code}</span>
                    </div>
                  </td>
                  <td className="px-8 py-5">
                    <span className="text-sm text-zinc-600 font-medium">{item.teacher}</span>
                  </td>
                  <td className="px-8 py-5">
                    <span className="text-xs font-bold text-zinc-400 bg-zinc-100 px-2 py-1 rounded">{item.students}</span>
                  </td>
                  <td className="px-8 py-5 text-right">
                    <div className="flex justify-end gap-2">
                      <button onClick={() => openEditModal(item)} className="p-2 text-zinc-400 hover:text-blue-600 hover:bg-blue-50 rounded-lg transition-all">
                        <Edit3 size={16} />
                      </button>
                      <button onClick={() => setDeleteConfirm(item.id)} className="p-2 text-zinc-400 hover:text-red-600 hover:bg-red-50 rounded-lg transition-all">
                        <Trash2 size={16} />
                      </button>
                    </div>
                  </td>
                </tr>
              )) : (
                <tr>
                   <td colSpan={4} className="px-8 py-20 text-center text-zinc-400 text-sm italic">
                      No classes found matching your search.
                   </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
      </div>

      {/* --- MODAL: CREATE/EDIT --- */}
      {isModalOpen && (
        <div className="fixed inset-0 z-50 flex items-center justify-center p-4 bg-zinc-900/60 backdrop-blur-sm animate-in fade-in duration-300">
          <div className="bg-white rounded-[2.5rem] w-full max-w-md p-8 shadow-2xl overflow-hidden relative">
            <button onClick={() => setIsModalOpen(false)} className="absolute top-6 right-6 p-2 text-zinc-400 hover:bg-zinc-100 rounded-full">
              <X size={20} />
            </button>
            
            <h2 className="text-xl font-bold mb-1">{editingClass ? "Edit Class" : "Create New Class"}</h2>
            <p className="text-sm text-zinc-500 mb-8">Define the class name and the specific arm code.</p>

            <form onSubmit={handleSave} className="space-y-5">
              <Input label="Class Name" value={formData.name} onChange={(v) => setFormData({...formData, name: v})} placeholder="e.g. JSS 2" required />
              <div className="grid grid-cols-2 gap-4">
                <Input label="Arm Code (A-Z)" value={formData.code} onChange={(v) => setFormData({...formData, code: v.toUpperCase()})} placeholder="A" maxLength={1} required />
                <div className="flex flex-col justify-end pb-3 text-[10px] font-bold text-zinc-300 uppercase italic">
                  Preview: {formData.name} {formData.code}
                </div>
              </div>
              <Input label="Assign Teacher" value={formData.teacher} onChange={(v) => setFormData({...formData, teacher: v})} placeholder="Full Name" />
              
              <button type="submit" className="w-full py-4 bg-zinc-900 text-white rounded-2xl font-bold shadow-xl hover:bg-zinc-800 transition-all mt-4">
                {editingClass ? "Update Class" : "Confirm & Create"}
              </button>
            </form>
          </div>
        </div>
      )}

      {/* --- MODAL: DELETE CONFIRM --- */}
      {deleteConfirm && (
        <div className="fixed inset-0 z-[60] flex items-center justify-center p-4 bg-zinc-900/20 backdrop-blur-md">
          <div className="bg-white rounded-3xl p-8 max-w-sm w-full text-center shadow-2xl border border-zinc-100">
            <div className="w-16 h-16 bg-red-50 text-red-500 rounded-full flex items-center justify-center mx-auto mb-4">
              <AlertCircle size={32} />
            </div>
            <h3 className="text-lg font-bold text-zinc-900">Delete Class?</h3>
            <p className="text-sm text-zinc-500 mt-2 mb-8">This action is permanent. All student associations for this arm will be disconnected.</p>
            <div className="flex gap-3">
              <button onClick={() => setDeleteConfirm(null)} className="flex-1 py-3 bg-zinc-100 text-zinc-600 rounded-xl font-bold text-sm">Cancel</button>
              <button onClick={() => handleDelete(deleteConfirm)} className="flex-1 py-3 bg-red-600 text-white rounded-xl font-bold text-sm">Delete</button>
            </div>
          </div>
        </div>
      )}

    </div>
  );
}

// --- REUSABLES ---

function StatCard({ icon, label, value, color }: any) {
  return (
    <div className="bg-white p-6 rounded-[2rem] border border-zinc-100 shadow-sm flex items-center gap-5">
      <div className={`w-12 h-12 rounded-2xl bg-zinc-50 flex items-center justify-center ${color}`}>
        {icon}
      </div>
      <div>
        <p className="text-[10px] font-bold text-zinc-400 uppercase tracking-widest">{label}</p>
        <p className="text-2xl font-black text-zinc-900">{value}</p>
      </div>
    </div>
  );
}

function Input({ label, value, onChange, placeholder, required, maxLength }: any) {
  return (
    <div className="space-y-1.5">
      <label className="text-[10px] font-bold text-zinc-400 uppercase tracking-wider ml-1">{label}</label>
      <input 
        required={required}
        maxLength={maxLength}
        value={value}
        onChange={(e) => onChange(e.target.value)}
        placeholder={placeholder}
        className="w-full px-4 py-3 bg-zinc-50 border border-zinc-200 rounded-xl text-sm font-medium outline-none focus:border-zinc-900 focus:bg-white transition-all"
      />
    </div>
  );
}